@import "../../../sass/utils/_colors.scss";

.search {
  display: flex;
  align-items: center;
  margin: 10px;

  height: 35px;
  background-color: map-get($colors, primary);
  border: 2px solid map-get($colors, accent);
  border-radius: 20px;

  svg {
    margin-left: 15px;
    fill: map-get($colors, secondary);
  }
}
